// SERVICES SECTION

.services {
    &_header {
        .text {
            color: #162739;
        }
        &-list {
            &_item {
                color: $blackalt;
                margin-bottom: 15px;
                &:last-of-type {
                    margin-bottom: 0;
                }
                .icon {
                    color: $secondary;
                    margin-right: 10px;
                    font-size: 16px;
                }
            }
        }
    }
}
