// STYLING FOR THE TINY SLIDER CONTROLS (ARROWS)

.tns-arrows {
    display: flex;
    align-items: center;

    &_arrow {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 40px;
        height: 40px;
        border: 1px solid $primary;
        font-size: 16px;
        border-radius: 50%;
        transition: all .3s ease-in-out;

        .icon {
            transition: all .2s ease;
        }

        &[aria-disabled="true"] {
            color: $gray;
            cursor: default;
            border-color: #C8C8C8;
        }

        &--prev {
            margin-right: 30px;
            &:hover:not([aria-disabled="true"]) {
                .icon {
                    margin-right: 5px;
                }
            }
        }

        &--next {
            &:hover:not([aria-disabled="true"]) {
                .icon {
                    margin-left: 5px;
                }
            }
        }
    }
}

